.product-section h1,
.title-container {
  display: flex;
  justify-content: center;
  text-align: center;
  margin: auto;
  font-family: var(--f-b), serif;
  color: var(--c-s);
  font-size: 1.5rem;
}
.product-section h1 {
  font-size: 2rem;
}
.title-container h2 {
  margin: 2rem 2rem;
  border-bottom: 1px solid transparent;
}
.title-container h2:hover {
  border-bottom: 1px solid var(--c-s);
  cursor: pointer;
}
.product-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  max-width: 1200px;
  padding: 1rem 0;
  margin: auto;
}

.selected {
  text-decoration: 9px solid var(--c-s) underline;
}

@media screen and (max-width: 800px) {
  .title-container {
    margin: 1rem 0rem;
    font-size: 1.2rem;
  }
  .title-container h1 {
    margin: 2rem 1rem;
    border-bottom: 1px solid transparent;
  }
}
