.user-data-form,
.cart-items-container {
  height: 100%;
  display: flex;
  text-align: center;
  justify-content: center;
  flex-direction: column;
}
.cart-links {
  height: 15rem;
  max-height: 15rem;
  overflow-y: auto;
}
.cart-items {
  margin-bottom: 1rem;
}

.cart-total {
  margin-top: 1rem;
  font-size: 1.5rem;
}
.cart-title {
  font-size: 2rem;
  display: flex;
  justify-content: center;
  font-weight: 400;
}
.cart-title,
.cart-links,
.cart-links li {
  color: var(--c-s);
}
.item-name,
.total-price {
  text-transform: capitalize;
}
.total-price {
  font-family: var(--f-a), serif;
  font-size: 1.8rem;
  color: var(--c-q);
}
.item-separator,
.item-delete {
  color: var(--c-q);
}
.item-list span {
  margin: 0 0.3rem;
}
.item-list {
  margin: 0.7rem 0;
}
.item-delete:hover {
  cursor: pointer;
}
.user-data-form label {
  font-family: var(--f-b), serif;
  color: var(--c-s);
  text-align: left;
}
.user-data-form input,
.user-data-form textarea {
  color: var(--c-q);
  font-weight: bold;
  border: none;
}
.user-data-form textarea {
  border: 1px solid var(--c-s);
}
.user-data-form input {
  border-bottom: 1px solid var(--c-s);
}
.user-data-form textarea:focus,
.user-data-form input:focus {
  outline: none;
}
.success-icon-container {
  font-size: 5rem;
  color: var(--c-s);
}
.success-icon-container:hover {
  cursor: pointer;
}
.order-recieved-modal {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  flex-direction: column;
}
.red {
  color:crimson!important
}
